import Vue from 'vue'
import Router from 'vue-router'

const noDataindex = resolve => require(['../views/addlist/noDataindex.vue'], resolve)
const addWrite = resolve => require(['../views/addWrite'], resolve)
const addlist = resolve => require(['../views/addlist'], resolve)
const setSecreat = resolve => require(['../views/setSecreat'], resolve)
const setPay = resolve => require(['../views/setSecreat/setPay.vue'], resolve)
const revise = resolve => require(['../views/setSecreat/revise.vue'], resolve)
const set = resolve => require(['../views/set'], resolve)
const phone = resolve => require(['../views/phone'], resolve)
const nice = resolve => require(['../views/zxhw/zxhw'], resolve)
const nicetop50 = resolve => require(['../views/zxhw/zxhwtop50'], resolve)
const SelfCenter = resolve => require(['../views/SelfCenter/SelfCenter.vue'], resolve)
const PrivilegeList = resolve => require(['../views/PrivilegeList/PrivilegeList.vue'], resolve)
const PrivilegeDetail = resolve => require(['../views/PrivilegeDetail/PrivilegeDetail.vue'], resolve)
const MyCollection = resolve => require(['../views/MyCollection/MyCollection.vue'], resolve)
const Check = resolve => require(['../views/check/Check'], resolve)
const SysMessage = resolve => require(['../views/message/SysMessage'], resolve)
const Message = resolve => require(['../views/message/Message'], resolve)
const MyAssets = resolve => require(['../views/MyAssets/MyAssets'], resolve)
const login = resolve => require(['../views/login.vue'], resolve)
const bag = resolve => require(['../views/gwd.vue'], resolve)
const payment = resolve => require(['../views/fk.vue'], resolve)
const buy = resolve => require(['../views/mdx.vue'], resolve)
const confirm = resolve => require(['../views/qrdd.vue'], resolve)
const invite = resolve => require(['../views/hdyq.vue'], resolve)
const change = resolve => require(['../views/lqb.vue'], resolve)
const index = resolve => require(['../views/index.vue'], resolve)
const edit = resolve => require(['../views/bjlm.vue'], resolve)
const question = resolve => require(['../views/cjwt.vue'], resolve)
const question1 = resolve => require(['../views/cjwt1.vue'], resolve)
const data = resolve => require(['../views/grzl.vue'], resolve)
const graduate1 = resolve => require(['../views/xqy1.vue'], resolve)
const graduatekf1 = resolve => require(['../views/xqy-kf1.vue'], resolve)
const editaddress = resolve => require(['../views/editaddress.vue'], resolve)
const privilege = resolve => require(['../views/privilege.vue'], resolve)
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/', // 孕娘管家
      name: 'index',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: index

    },
    {
      path: '/login', // 登陆
      // name: 'login',
      component: login
    },
    {
      path: '/addlist', // 地址列表
      name: 'addlist',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: addlist
    },
    {
      path: '/addWrite', // 地址填写
      name: 'addWrite',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: addWrite
    },
    {
      path: '/noDataindex', // 没有地址
      name: 'noDataindex',
      component: noDataindex
    },
    {
      path: '/setSecreat', // 密码管理
      name: 'setSecreat',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: setSecreat
    },
    {
      path: '/set', // 设置
      name: 'set',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: set
    },
    {
      path: '/setPay', // 设置支付密码
      name: 'setPay',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: setPay
    },
    {
      path: '/revise', // 修改卡密
      name: 'revise',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: revise
    },
    {
      path: '/phone', // 验证手机号
      name: 'phone',
      component: phone
    },
    {
      path: '/question', // 常见问题
      name: 'question',
      component: question
    },
    {
      path: '/question1', // 常见问题1
      name: 'question1',
      component: question1
    },
    {
      path: '/myAssets', // 我的资产
      name: 'MyAssets',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: MyAssets
    }, {
      path: '/message', // 消息中心
      name: 'Message',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: Message
    }, {
      path: '/sysmessage', // 暂无消息
      name: 'SysMessage',
      component: SysMessage
    }, {
      path: '/check', // 孕娘支票
      name: 'Check',
      component: Check
    }, {
      path: '/selfCenter', // 我
      name: '我',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: SelfCenter
    }, {
      path: '/privilegeList', // 特权列表
      name: '特权列表',
      component: PrivilegeList
    }, {
      path: '/myCollection', // 我的收藏
      name: '我的收藏',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: MyCollection
    }, {
      path: '/privilegeDetail', // 特权详情页
      name: '特权详情页',
      component: PrivilegeDetail
    }, {
      path: '/nice', // 专享好物-上新
      name: 'nice',
      component: nice
    }, {
      path: '/rexiao', // 专享好物top50-热销
      name: 'rexiao',
      component: nicetop50
    }, {
      path: '/bag', // 购物袋
      name: 'bag',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: bag
    }, {
      path: '/payment', // 付款
      name: 'payment',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: payment
    }, {
      path: '/buy', // 买东西
      name: 'buy',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: buy
    }, {
      path: '/confirm', // 确认订单
      name: 'confirm',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: confirm
    }, {
      path: '/invite', // 活动邀请
      name: 'invite',
      component: invite
    }, {
      path: '/change', // 零钱包
      name: 'change',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: change
    },
    {
      path: '/edit', // 编辑栏目
      component: edit
    },
    {
      path: '/data', // 个人资料
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: data
    },
    {
      path: '/graduate1', // 创意研究所1
      component: graduate1
    },
    {
      path: '/graduatekf1', // 创意研究所2
      component: graduatekf1
    },
    {
      path: '/editaddress', // 编辑地址
      name: 'editaddress',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: editaddress
    },
    {
      path: '/privilege', // 特权编辑
      name: 'privilege',
      meta: {
        requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
      },
      component: privilege
    }
  ]
})



