import axios from 'axios'
import qs from 'qs'
// import { error } from 'util';
import {
  Toast
} from 'vant'

import router from '../src/router'

var instance = axios.create({});

axios.defaults.timeout = 5000;
axios.defaults.baseURL = '/';
axios.defaults.headers.common['appkey'] = 'guanjia';
axios.defaults.headers.common['appsecret'] = '123456';
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded'

// http request 拦截器
//POST传参序列化
axios.interceptors.request.use((config) => {
  if(config.method == 'post'){
    config.data = qs.stringify(config.data);
  }
  if(window.localStorage.getItem('token')){
    axios.defaults.headers['token'] = window.localStorage.getItem('token')
  }
  // console.log(window.localStorage.getItem('token'));
  // console.log(config)
  return config;
},(error) =>{
  Toast("error");
});

//http response 拦截器
//code状态码200判断
axios.interceptors.response.use((res) =>{
  if(res.data.code == '0'){
    Toast(res.data.msg);
  }
  console.log('http response 拦截器',res);
  if(res.data.code == '2' || res.data.code == '422'){
    Toast('登录失败，请重新登录。')
    localStorage.removeItem('token')
    router.push({path: '/login'})
  }

  return res;
}, (error) => {
  Toast.fail("网络异常");
  if(localStorage.getItem('token')){
    localStorage.removeItem('token')
  }
  router.push({path: '/login'})
});

/**
 * get 请求方法
 * @param {*} url
 * @param {*} params
 * @returns {Promise}
 */

export function fetch(url, params ={}) {

  return new Promise((resolve, reject) => {
    axios.get(url, {
      params: params
    })
    .then(response => {
      resolve(response);
    })
    .catch(err => {
      reject(err)
    })
  })
}


/**
 * post 请求方法
 * @param {*} url
 * @param {*} data
 * @returns {Promise}
 */
export function post(url, data = {}) {

  return new Promise((resolve, reject) => {
    axios.post(url, data)
        .then(response => {
          console.log('response ',response);
          resolve(response.data);
        }, err => {
          reject(err);
        })
  })
}

/**
 * put 请求方法
 * @param {*} url
 * @param {*} data
 * @returns {Promise}
 */
export function put(url, data = {}) {

  return new Promise((resolve, reject) => {
    axios.put(url, data)
        .then(response => {
          resolve(response.data);
        }, err=> {
          reject(err);
        })
  })
}

/**
 * patch 请求方法
 * @param {*} url
 * @param {*} data
 */

export function patch(url, data = {}) {

  return new Promise((resolve, reject) => {
    axios.patch(url, data)
        .then(response => {
          resolve(response.data);
        }, err=> {
          reject(err);
        })
  })
}
export default axios;



